.main-container {
  width: 100%;
  max-width: 1200px;
  margin: auto; }

.main-header {
  display: flex;
  flex-wrap: wrap; }
  .main-header-1 {
    width: 100%; }
    @media screen and (min-width: 640px) {
      .main-header-1 {
        width: 20%; } }
  .main-header-2 {
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: flex-end; }
    @media screen and (max-width: 640px) {
      .main-header-2 {
        display: none; } }
  .main-header .donate {
    display: none; }
    @media screen and (min-width: 760px) {
      .main-header .donate {
        display: block; } }

.section-center {
  text-align: center;
  width: 90%;
  margin: auto; }

.card-section {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center; }
  @media screen and (min-width: 640px) {
    .card-section {
      flex-wrap: nowrap; } }

.section-background__content {
  padding: 1.5rem 0; }
  @media screen and (min-width: 640px) {
    .section-background__content {
      padding: 5rem; } }

.section-background__cards {
  display: flex;
  flex-wrap: wrap; }
  @media screen and (min-width: 640px) {
    .section-background__cards {
      flex-wrap: nowrap; } }
  .section-background__cards__content {
    margin: 1em auto; }
    @media screen and (min-width: 640px) {
      .section-background__cards__content {
        margin: 1em;
        padding: 5rem; } }

.p-2 {
  padding: 3rem !important; }

.footer {
  padding: 3rem; }
  .footer-widget__about {
    grid-area: about; }
  .footer-widget__company {
    grid-area: company; }
  .footer-widget__services {
    grid-area: services; }
  .footer-widget__subscribe {
    grid-area: subscribe; }
  @media screen and (min-width: 640px) {
    .footer {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-areas: "about company services subscribe"; } }
  @media screen and (min-width: 760px) {
    .footer {
      grid-template-columns: 2fr 1fr 1fr 2fr;
      grid-template-areas: "about company services subscribe"; } }

.main-header {
  padding: 16px; }

.logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%; }
  @media screen and (min-width: 640px) {
    .logo {
      width: 100%; } }

.main-nav {
  background: #ed5807;
  display: none; }
  @media screen and (min-width: 760px) {
    .main-nav {
      display: flex;
      justify-content: space-between;
      max-width: 1200px;
      margin: auto;
      position: relative;
      top: 1.5rem;
      z-index: 100; } }
  .main-nav.show {
    display: block; }

.main-menu {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px; }
  @media screen and (min-width: 760px) {
    .main-menu {
      display: flex; } }
  .main-menu__item {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  .main-menu__link {
    display: block;
    line-height: 2;
    padding: 0.5em 1em;
    text-decoration: none;
    color: #fff; }

.social-networks a {
  font-size: 16px;
  text-decoration: none;
  color: #fff;
  display: inline-block;
  line-height: 3em;
  padding: 0 1em; }

.toggle-menu {
  color: #ed5807;
  display: table;
  font-size: 1.6em;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer; }
  @media screen and (min-width: 760px) {
    .toggle-menu {
      display: none; } }

.main-banner {
  position: relative; }
  .main-banner::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to left, #ed5807, #25201d);
    opacity: 0.4; }
  .main-banner__data {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 75%; }
    @media screen and (min-width: 760px) {
      .main-banner__data {
        justify-content: flex-start;
        left: 40%;
        width: 50%; } }
  .main-banner__subtitle, .main-banner__description {
    display: none;
    width: 100%; }
    @media screen and (min-width: 760px) {
      .main-banner__subtitle, .main-banner__description {
        display: block;
        color: #fff; } }
  .main-banner__subtitle {
    font-size: 16px;
    margin: 0; }
    .main-banner__subtitle:after {
      content: '';
      display: block;
      width: 20%;
      height: 2px;
      background: #ed5807; }
  .main-banner__title {
    margin: 0 0 0.5em;
    width: 100%;
    text-align: center;
    color: #ed5807; }
    @media screen and (min-width: 760px) {
      .main-banner__title {
        text-align: start; } }
  .main-banner__img {
    display: block;
    width: 100%; }

.section-center {
  padding: 1rem; }
  .section-center__title {
    font-size: 24px; }
    .section-center__title span {
      color: #ed5807; }

.section-images {
  padding: 1em 1em; }
  .section-images__img {
    display: block; }
  .section-images__footer {
    position: relative; }
  .section-images__content {
    background: #ed5807;
    display: block;
    padding: 0.5em .5em;
    margin: 0;
    width: 65%;
    color: #fff; }
  .section-images__icon {
    width: 4em;
    height: 3.5em;
    background: #25201d url(data:image/svg+xml;utf8;base64,) center/1.5rem no-repeat;
    position: absolute;
    left: 60%;
    bottom: 0;
    clip-path: polygon(25% 0, 75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%); }

.section-background {
  background: url(https://images.pexels.com/photos/1329061/pexels-photo-1329061.jpeg?auto=compress&cs=tinysrgb&h=650&w=940) center/cover no-repeat; }
  .section-background-2 {
    background: url(https://images.pexels.com/photos/1029615/pexels-photo-1029615.jpeg?auto=compress&cs=tinysrgb&h=650&w=940) center/cover no-repeat;
    color: #fff; }
  .section-background__cards__content {
    width: 90%;
    text-align: center;
    padding: 1em 0;
    background: rgba(237, 88, 7, 0.5); }
    .section-background__cards__content img {
      width: 50px; }

.logo-footer {
  width: 100%; }

.footer {
  background: #25201d;
  font-size: 16px;
  color: #fff; }
  .footer-widget__logo {
    width: 100px; }
  .footer-widget__company ul, .footer-widget__services ul {
    list-style: none;
    padding: 0 1rem; }
    .footer-widget__company ul li a, .footer-widget__services ul li a {
      text-decoration: none;
      color: #ed5807; }
  .footer-widget__about, .footer-widget__company, .footer-widget__services, .footer-widget__subscribe {
    padding: 0.5em; }
    .footer-widget__about h2, .footer-widget__company h2, .footer-widget__services h2, .footer-widget__subscribe h2 {
      margin: 0; }

.contact-widget {
  display: flex;
  padding: 0.5em; }
  .contact-widget__icon {
    background: #25201d;
    color: #fff;
    width: 2em;
    height: 2em;
    text-align: center;
    line-height: 2.4em;
    font-size: 1.3em; }
  .contact-widget__data {
    padding: 0 1em; }
  .contact-widget__title {
    margin: 0; }
  .contact-widget__content {
    margin: 0; }

.button {
  text-decoration: none;
  padding: 0 1em;
  line-height: 3;
  display: inline-block; }
  .button--dark {
    background: #25201d;
    color: #fff; }
  .button--light {
    background: #fff;
    color: #ed5807; }
  .button--cta {
    background: #ed5807;
    color: #fff; }
  .button--sqare {
    padding: 0;
    line-height: 2.4em; }
